* {
    margin: 0;
    padding: 0;
}

.body {
    margin: 20px auto 0;
    height: 600px;
    width: 1200px;
    position: relative;

}

.login {
    position: absolute;
    top: 120px;
    right: 60px;
    width: 350px;
    box-shadow: 0px 3px 10px 0 rgba(0, 0, 0, .06);
    background-color: #fafafa;
}

.login_form {
    position: relative;
    height: 350px;
    margin: 35px;

}

.login_tab {
    margin-bottom: 30px;
}

span {
    cursor: pointer;
    margin: 9px;
}

.login_active {
    color: #f52b00;
    font-weight: 700;
    border-bottom: 2px solid #f52b00;
}

input {
    outline: none;
    width: 100%;
    height: 42px;
    font-size: 14px;
    margin-bottom: 20px;
    border: none;
    padding-left: 20px;
    box-sizing: border-box;
    /* 保持总宽度不变 */
}

button {
    width: 100%;
    height: 42px;
    font-size: 14px;
    margin-bottom: 20px;
    border: none;
    cursor: pointer;
    background-color: #f52b00;
    color: white;
}

.code_input {
    width: 100%;
    height: 42px;
    margin-bottom: 20px;
    display: flex;
}

.code_input input {
    outline: none;
    flex: 1;
    height: 42px;
    font-size: 14px;
    margin-bottom: 20px;
    border: none;
}

.code_input button {
    width: 70px;
    height: 42px;
    font-size: 12px;
    margin-bottom: 20px;
    border: none;
    cursor: pointer;
    background-color: white;
    color: #f52b00;
}

.toRegister {
    position: absolute;
    bottom: 0;
    right: 0;
    color: #6c6c6c;
    font-size: 14px;
    cursor: pointer;
}

.toRegister:hover{
    text-decoration: underline;
}

.code_input .block{
    background-color: #6c6c6c;
    cursor: not-allowed;
    display: block;
}